#intro {
  width: 400px;
  margin-top: 20px;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0.19em;
  float: left;
  margin-right: 100px;
}

h1 {
  margin-top: 40%;
  font-weight: 300;
  font-size: 50px;
  letter-spacing: 0.245em;
  height: 100px;
}

#botao {
  margin-top: 32px;
  color: #fff;
  background-color: rgba(54, 38, 167, 1);
  width: 219px;
  height: 53px;
  border-radius: 10px 30px;
  font-family: Roboto Slab;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  transition: 0.3s;
  cursor: pointer;
  &:hover {
    transform: translate(-2px, -5px);
    filter: grayscale(10%);
    box-shadow: 6px 7px 6px rgba(54, 38, 167, 0.4);
  }
}

#home-img {
  width: 64px;
  height: 10px;
}

#flex {
  display: flex;
}
